@import url('https://fonts.googleapis.com/css2?family=Noto+Sans&display=swap');

* {
    margin: 0;
    padding: 0;
}

.loader {
    width: 100vw;
    height: 100vh;
    display: flex;
    justify-content: center;
    align-items: center;
    position: fixed;
    top: 0;
    left: 0;
    background-color: #eee;
    letter-spacing: 0.8px;
}

.form-disp {
    width: 100vw;
    height: 90vh;
    display: flex;
    justify-content: center;
    align-items: center;
    font-family: 'Lucida Sans', 'Lucida Sans Regular', 'Lucida Grande', 'Lucida Sans Unicode', Geneva, Verdana, sans-serif !important;
}

.form-container {
    width: 50%;
    height: auto;
    background-color: #eee;
    border-radius: 20px;
    padding: 50px;
}

.test-body {
    width: 100vw;
    height: 100vh;
    background-color: #eee;
    display: flex;
    justify-content: center;
}

.test-body form {
    width: 100vw;
    height: 100vh;
    background-color: #fff;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}

.test-body form div:nth-child(3) {
    height: auto;
}

.testPageDisp>form {
    display: flex;
    flex-direction: column;
    height: 90vh;
}

.testPageDisp>form>textarea {
    height: 100%;
    width: 100vw;
    border-radius: 0;
    border: 0 solid #0000;
    resize: none;
    padding: 10px;
    outline: none;
    font-family: 'Noto Sans';
}

.testPageDisp>form>button {
    height: 5%;
    border-radius: 0;
    font-family: 'Noto Sans';
    line-height: 100%;
}