.navbar {
    width: 100vw;
    height: 10vh;
    max-height: 10vh;
    min-height: 50px;
    background-color: #eee;
    user-select: none;
    overflow: hidden;
    padding: 0;
}

.nav-title {
    display: flex;
    flex-direction: row;
    align-items: center;
    gap: 1vw;
}

.nav-title img {
    max-height: 100px;
    height: 10vh;
    min-width: 30px;
    vertical-align: middle;
}

.nav-title span {
    font-family: 'Noto Sans';
    font-size: 1.5rem;
    line-height: 100%;
    height: 100%;
    text-align: center;
}

.nav-logo{
    height: 100%;
    padding: 1pt;
}